<template>
  <div class="preview-box">
    <div class="btns">
      <div class="title">
        <div class="back" @click="close">
          <v-icon name="arrow-left" scale="1.5" />
        </div>
        {{ title }}
      </div>
    </div>
    <slider animation="fade" :autoplay="false" width="100%" height="100%">
      <slider-item v-for="item in list" :key="item.id">
        <!-- <p style="line-height: 280px; font-size: 5rem; text-align: center;">Page{{ index + 1 }}</p> -->
        <img :src="item.src" @load="load($event)" :class="{ ratio: ratio }" />
      </slider-item>
    </slider>
  </div>
</template>
<script>
export default {
  props: {
    url: String,
    title: String
  },
  data() {
    return {
      ratio: false,
      list: []
    }
  },
  mounted() {
    this.$request
      .get(this.url)
      .then(res => {
        this.list = res.data.map(item => {
          item.src = this.$getContent(item.body, true).image
          return item
        })
      })
      .catch(e => {
        console.error(e)
      })
  },
  methods: {
    close() {
      this.$parent.close()
    },
    load(e) {
      let img = e.path[0]
      if (img.naturalWidth == img.naturalHeight && window.innerWidth > window.innerHeight) {
        this.ratio = true
      } else if (img.naturalWidth > img.naturalHeight) {
      } else if (img.naturalHeight > img.naturalWidth) {
        this.ratio = true
      }
    }
  }
}
</script>
<style>
.preview-box {
  width: 100%;
  height: 100%;
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background: rgba(0, 0, 0, 0.75);
}

.slider .slider-item {
  display: flex;
  justify-content: center;
  align-items: center;
}

.slider .slider-item img {
  max-width: 100%;
}

.slider .slider-item img.ratio {
  width: auto;
  height: 100%;
}
.preview-box .btns {
  background: linear-gradient(to bottom, rgba(0, 0, 0, 1) 0%, rgba(0, 0, 0, 0) 100%);
  height: 60px;
  position: absolute;
  top: 0;
  right: 0;
  left: 0;
  z-index: 9999;
}
.preview-box .btns .back {
  width: 40px;
  height: 60px;
  display: block;
  color: #fff;
  cursor: pointer;
  line-height: 60px;
  position: absolute;
  left: 0;
}
.title {
  color: #fff;
  height: 60px;
  line-height: 45px;
  text-align: center;
  width: 100%;
  position: relative;
}
</style>
