<template>
  <div>
    <div id="map"></div>
  </div>
</template>
<script>
import 'ol/ol.css'
import Map from 'ol/Map'
import View from 'ol/View'
import { defaults as defaultControls, ScaleLine } from 'ol/control'
import TileLayer from 'ol/layer/Tile'
import WMTS from 'ol/source/WMTS'
import { Vector as VectorLayer } from 'ol/layer'
import { getWidth, getTopLeft } from 'ol/extent.js'
import { get as getProjection } from 'ol/proj.js'
import WMTSTileGrid from 'ol/tilegrid/WMTS'
import VectorSource from 'ol/source/Vector'
import Point from 'ol/geom/Point'
import Feature from 'ol/Feature'
import { Icon, Style } from 'ol/style'
import Overlay from 'ol/Overlay'

let projection = getProjection('EPSG:4326')
let maxZoom = 19
let resolutions = []
let matrixIds = []
for (let res = 0; res < maxZoom; res++) {
  matrixIds[res] = res
  resolutions[res] = getWidth(projection.getExtent()) / 256 / Math.pow(2, res)
}
let tileGrid = new WMTSTileGrid({
  origin: getTopLeft(projection.getExtent()),
  resolutions: resolutions,
  matrixIds: matrixIds,
})

export default {
  data() {
    return {
      photos: [],
      map: null,
      source: new VectorSource({
        features: [],
      }),
      base: 1,
    }
  },
  methods: {
    getImage(content, item) {
      let _this = this
      let img = document.createElement('img')
      img.src = content.image
      let wrapper = document.createElement('div')
      wrapper.className = ['photo']
      wrapper.appendChild(img)
      img.onclick = () => {
        _this.$preview({
          url: item.comments_url,
          title: item.title,
        })
      }
      return wrapper
    },
    getStyle(content) {
      const _this = this
      return new Style({
        image: new Icon({
          //   src: content.image,
          img: _this.getImage(content),
          imgSize: [40, 40],
          radius: 40,
          size: [40, 40],
        }),
      })
    },
    checkNext(link) {
      const _this = this
      if (/next/.test(link)) {
        this.base++
        let nextPage = process.env.VUE_APP_API_NEXT + '?page=' + this.base
        setTimeout(() => {
          _this.getPhotos(nextPage)
        }, 500)
      }
    },
    getPhotos(url) {
      const _this = this
      this.$request
        .get(url)
        .then((res) => {
          res.data.forEach((item) => {
            let content = this.$getContent(item.body)
            this.map.addOverlay(
              new Overlay({
                element: _this.getImage(content, item),
                positioning: 'center-center',
                stopEvent: true,
                offset: [0, 0],
                position: content.location,
              })
            )
          })
          _this.checkNext(res.headers.link)
        })
        .catch((e) => {
          console.error(e)
        })
    },
  },
  mounted() {
    const _this = this
    let layers = [
      new TileLayer({
        source: new WMTS({
          url: 'http://t0.tianditu.gov.cn/img_c/wmts',
          layer: 'img',
          version: '1.0.0',
          matrixSet: 'c',
          format: 'tile',
          style: 'default',
          tileGrid: tileGrid,
          tileLoadFunction(tile, src) {
            tile.getImage().src = src + '&tk=' + process.env.VUE_APP_MAP_TOKEN
          },
        }),
      }),
      new TileLayer({
        source: new WMTS({
          url: 'http://t0.tianditu.gov.cn/cia_c/wmts',
          layer: 'cia',
          version: '1.0.0',
          matrixSet: 'c',
          format: 'tile',
          style: 'default',
          tileGrid: tileGrid,
          tileLoadFunction(tile, src) {
            tile.getImage().src = src + '&tk=' + process.env.VUE_APP_MAP_TOKEN
          },
        }),
      }),
      new VectorLayer({
        source: this.source,
      }),
    ]
    const view = new View({
      projection: projection,
      center: [110.8724870281715, 37.400202955280264],
      zoom: 5,
    })
    this.map = new Map({
      layers: layers,
      target: 'map',
      view: view,
    })
    this.map.on('click', (e) => {
      console.log(JSON.stringify(e.coordinate))
    })

    // view.on('change:resolution', e => {
    //   let zoom = view.getZoom()
    //   if (zoom % 1 == 0) {
    //     let overlays = this.map.getOverlays()
    //     console.log(overlays)
    //   }
    // })
    this.getPhotos(process.env.VUE_APP_API)
  },
}
</script>
<style>
#map {
  height: 100vh;
  width: 100vw;
}
.photo {
  width: 40px;
  height: 40px;
  border-radius: 40px;
  overflow: hidden;
  border: 2px solid #fff;
  position: relative;
}
@keyframes show {
  0% {
    opacity: 0;
  }
  100% {
    opacity: 1;
  }
}
.photo img {
  width: 90px;
  height: auto;
  cursor: pointer;
  position: absolute;
  left: -50%;
  right: -50%;
  top: -50%;
  bottom: -50%;
  margin: auto;
  animation: show 0.5s;
}
</style>
