import previewBox from './preview.vue'
export default {
  install(Vue) {
    Vue.prototype.$preview = img => {
      const preview = new Vue({
        render(h) {
          return h(previewBox, {
            props: {
              url: img.url,
              title: img.title
            }
          })
        },
        methods: {
          close() {
            this.$destroy()
            document.body.removeChild(this.$el)
          }
        }
      })
      let component = preview.$mount()
      document.body.appendChild(component.$el)
    }
  }
}
